import React from 'react'
import logo from '../../assets/dashboard.svg';
export default {
    dashboard: {
        text: 'Dashboard',
        icon: <img src={logo} alt="logo" className="fas dashboard"/>,
        path: 'dashboard'
    },
    profile: {
        text: 'Profile',
        icon: <i className="fas fa-user" />,
        path: 'profile'
    },
    bands: {
        text: 'Bands',
        icon: <i className="fas fa-users" />,
        path: 'bands'
    },
    setlist: {
        text: 'Setlists',
        icon: <i className="fas fa-list" />,
        path: 'setlists'
    },
}